.product-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 17px;
  padding-top: 19px;
  padding-right: 20px;
  padding-bottom: 25px;
  padding-left: 20px;
  border-bottom: 1px solid $second-border-color;

  @media (min-width: $mobile-above) {
    row-gap: 32px;
    width: 100%;
    max-width: 324px;
    padding-top: 10px;
    padding-right: 37px;
    padding-bottom: 40px;
    padding-left: 37px;
    background-image: linear-gradient(to bottom, $base-background-light-color 0%, $base-background-light-color 16%, $second-background-color 16%, $second-background-color 100%);
  }

  @media (min-width: $desktop-above) {
    width: 100%;
    max-width: 245px;
    padding-right: 38px;
    padding-left: 38px;
  }
}

.product-card__wrapper {
  display: flex;
  justify-content: center;

  @media (min-width: $mobile-above) {
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin-bottom: auto;
  }
}

.product-card__image-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 140px;

  @media (min-width: $mobile-above) {
    max-width: 200px;
    min-height: 210px;
    margin-bottom: 22px;
  }

  @media (min-width: $desktop-above) {
    margin-bottom: 12px;
  }
}

.product-card__image {
  display: block;
  width: 100%;
  object-fit: scale-down;
}

.product-card__meta {
  width: 140px;

  @media (min-width: $mobile-above) {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
}

.product-card__title {
  width: 100%;
  max-width: 76px;
  margin-top: 0;
  margin-bottom: 14px;
  font-family: $font-second;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.25;
  text-transform: uppercase;
  color: $catalog-product-card-title-color;

  @media (min-width: $mobile-above) {
    width: 100%;
    max-width: 135px;
    margin-right: auto;
    margin-bottom: 20px;
    margin-left: auto;
    font-size: 20px;
    line-height: 1.2;
    text-align: center;
  }

  @media (min-width: $desktop-above) {
    margin-bottom: 23.4px;
  }
}

.product-card__title-link {
  color: $catalog-product-card-title-color;
}

.product-card__info {
  margin-top: 0;
  margin-bottom: 0;

  @media (min-width: $mobile-above) {
    display: flex;
    flex-direction: column;
    row-gap: 7px;
    width: 100%;
    max-width: 250px;
  }

  @media (min-width: $desktop-above) {
    width: 100%;
    max-width: 169px;
  }
}

.product-card__info div {
  display: flex;
  justify-content: space-between;


  @media (min-width: $mobile-above) {
    padding-bottom: 4px;
    border-bottom: 1px solid $third-border-color;
  }

  @media (min-width: $desktop-above) {
    padding-bottom: 6.5px;
  }
}

.product-card__info dt {
  display: inline;
  font-size: 12px;
  line-height: 1.33;
  color: $second-text-dark-color;

  @media (min-width: $mobile-above) {
    font-size: 16px;
    line-height: 1.25;
  }
}

.product-card__info dd {
  display: inline;
  margin-left: 0;
  font-size: 12px;
  line-height: 1.33;
  color: $second-text-dark-color;

  @media (min-width: $mobile-above) {
    font-size: 16px;
    line-height: 1.25;
  }
}

.product-card__button.button--order {
  @media (min-width: $desktop-above) {
    width: 100%;
    max-width: 169px;
    height: 40px;
  }
}

.product-card__button-link {
  display: block;
  width: 100%;
  max-width: 280px;
  min-height: 40px;
  padding: 10px 40px;
  font-family: $font-second;
  font-size: 16px;
  line-height: 1.25;
  text-transform: uppercase;
  text-align: center;
  color: $base-text-light-color;
  background-color: $branded-color;

  @media (min-width: $mobile-above) {
    width: 100%;
    max-width: 250px;
  }

  @media (min-width: $desktop-above) {
    width: 100%;
    max-width: 169px;
  }

  &:hover,
  &:focus {
    background-color: $branded-color-hover;
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }
}
